// http://dean.edwards.name/packer/
angular
	.module("baoNguyenApp", [
		"common.fabric",
		"common.fabric.utilities",
		"common.fabric.constants",
		'colorpicker.module'
	])
	.controller("nguyenCtrl", [
		"$scope",
		"Fabric",
		"FabricConstants",
		"Keypress",
		function ($scope, Fabric, FabricConstants, Keypress) {
			$scope.fabric = {};
			$scope.FabricConstants = FabricConstants;


			$scope.saveImage = function () {
				$scope.fabric.download('nguyen_' + Math.random())
			};

			//
			// Creating Canvas Objects
			// ================================================================
			// $scope.addShape = function (path) {
			// 	$scope.fabric.addShape("http://fabricjs.com/assets/15.svg");
			// };
			$scope.addImage = function (image) {
				$scope.fabric.addImage(image);
			};
			$scope.addText = function () {
				$scope.fabric.setStroke(null)
				$scope.fabric.setStrokeWidth(1)
			};
			$scope.setImage = function () {
				$scope.fabric.setStroke(null)
				$scope.fabric.setStrokeWidth(0)
			};

			$scope.addRect = function (rc) {
				$scope.fabric.addRect(rc);
				$('#sharpModal').modal('hide');
				$scope.fabric.setStroke(null)
				$scope.fabric.setStrokeWidth(1)
			};

			$scope.addImageUpload = function (data) {
				var obj = angular.fromJson(data);
				$scope.addImage(obj.filename);
			};

			//
			// Editing Canvas Size
			// ================================================================
			$scope.selectCanvas = function () {
				$scope.canvasCopy = {
					width: $scope.fabric.canvasOriginalWidth,
					height: $scope.fabric.canvasOriginalHeight
				};
			};

			$scope.setCanvasSize = function () {
				$scope.fabric.setCanvasSize(
					$scope.canvasCopy.width,
					$scope.canvasCopy.height
				);
				$scope.fabric.setDirty(true);
				// delete $scope.canvasCopy;
			};

			//
			// Init
			// ================================================================
			$scope.init = function () {
				$scope.fabric = new Fabric({
					JSONExportProperties: FabricConstants.JSONExportProperties,
					textDefaults: FabricConstants.textDefaults,
					shapeDefaults: FabricConstants.shapeDefaults,
					json: {}
				});
				console.log('%cBAONGUYEN', 'font-size:100px;color:#ff451a;text-shadow:0 1px 0 #f73936,0 2px 0 #f73936 ,0 3px 0 #f73936 ,0 4px 0 #f73936 ,0 5px 0 #f73936 ,0 6px 1px rgba(0,0,0,.1),0 0 5px rgba(0,0,0,.1),0 1px 3px rgba(0,0,0,.3),0 3px 5px rgba(0,0,0,.2),0 5px 10px rgba(0,0,0,.25),0 10px 10px rgba(0,0,0,.2),0 20px 20px rgba(0,0,0,.15);')
				console.log('%c BAONGUYEN ' + '%c - The best Web Solutions Provider', 'border-radius: 2px; padding: 3px; background: #ff451a; color: #FFF', 'color: #ff451a')
				console.warn("BAONGUYEN warning: This is a browser feature intended for developers. If someone told you to copy and paste something here to enable a BAONGUYEN feature or 'hack' someone's account, it is a scam and will give them access to your BAONGUYEN account.")
				document.onkeyup = function (e) {
					var e = e || window.event;
					if (e.altKey && e.ctrlKey && e.shiftKey && e.which == 13) {
						alert('BAO NGUYEN - +(1) 682 203 1334\nEmail: baonguyenyam@gmail.com\nWeb: baonguyenyam.github.io')
						return false;
					}
				};
			};

			$scope.$on("canvas:created", $scope.init);

			Keypress.onSave(function () {
				$scope.updatePage();
			});
			$scope.caption = "";
			$scope.fileUrl = "";

			
			$scope.imageChange = function (inp) {
				file = inp.files[0];
				fr = new FileReader();
				fr.onload = createImage;
				fr.readAsDataURL(file);
				var img = null;
				function createImage() {
					img = new Image();
					img.onload = imageLoaded;
					img.src = fr.result;
				}

				function imageLoaded() {
					var fabImg = new fabric.Image(img);
					$scope.fabric.addImage(img.src);
				}
			};

			///// DRAWING
			$scope.DrawingMode  = [
				'Pencil',
				'Circle',
				'Spray',
				'Pattern'
			]
			$scope.csetDrawing = '#f00'
			$scope.ysetDrawing = '1'
			$scope.fsetDrawing = '0'
			$scope.ymk = ''
			$scope.sk = '0'
			$scope.blisterPackTemplateSelected = $scope.DrawingMode[0]
			$scope.setPaint = function () {
				if($scope.oPaint) {
					$scope.oPaint = !$scope.oPaint
					$scope.setFreeDrawingMode(!$scope.getFreeDrawingMode())
				} else {
					$('#paintModal').modal('show');
					$scope.oPaint = !$scope.oPaint
					$scope.setFreeDrawingMode(!$scope.getFreeDrawingMode())
				}
			}
			$scope.getFreeDrawingMode = function () {
				$scope.fabric.getFreeDrawingMode()
			}
			$scope.setFreeDrawingMode = function (e) {
				e = !$scope.fabric.getFreeDrawingMode();
				$scope.fabric.setFreeDrawingMode(e)
			}
			$scope.getDrawingMode = function () {
				$scope.fabric.getDrawingMode()
			}
			$scope.setDrawingMode = function (e) {
				$scope.fabric.setDrawingMode(e)
			}
			$scope.setDrawingLineShadowWidth = function (e) {
				$scope.fabric.setDrawingLineShadowWidth(e)
			}
			$scope.getDrawingLineShadowWidth = function () {
				$scope.fabric.getDrawingLineShadowWidth()
			}
			$scope.setDrawingLineColor = function (e) {
				$scope.fabric.setDrawingLineColor(e)
			}
			$scope.getDrawingLineColor = function () {
				$scope.fabric.getDrawingLineColor()
			}
			$scope.setDrawingLineWidth = function (e) {
				$scope.fabric.setDrawingLineWidth(e)
			}
			$scope.getDrawingLineWidth = function () {
				$scope.fabric.getDrawingLineWidth()
			}
			$scope.setStroke = function (e) {
				$scope.fabric.setStroke(e)
			}
			$scope.getStroke = function () {
				$scope.fabric.getStroke()
			}
			$scope.setStrokeWidth = function (e) {
				$scope.fabric.setStrokeWidth(e)
			}
			$scope.getStrokeWidth = function () {
				$scope.fabric.getStrokeWidth()
			}



		}
	]);

////////////

var invokeImageChange = function (that) {
	angular
		.element(that)
		.scope()
		.imageChange(that);
	angular
		.element(that)
		.scope()
		.$digest();
};

